import React from "react"
import logo from "../img/logo.png"
import cat from "../img/cat.png"
import cat2 from "../img/pumpkin.jpg"
import bridge from "../img/bridge.png"
import rain from "../img/black.png"
import Modal from "react-responsive-modal";
import PopPop from 'react-poppop';

export default class App extends React.Component {
    state = {
        open: false,
        modalImage: "",
        modal_des: "",
        modal_title: ""
      };
    
      onOpenModal = (img, modal_des, modal_title) => {
        this.setState({ open: true });
        this.setState({ modalImage: img });
        this.setState({ modal_des: modal_des });
        this.setState({ modal_title: modal_title })
      };
    
      onCloseModal = () => {
        this.setState({ open: false });
      };
    

    render() {
        const { open, modalImage, modal_des, modal_title } = this.state;
        var content = {
            transition: 'all 0.3s',
            backgroundColor: 'white',
            borderRadius: '3px',
            zIndex: 2000,
            position: 'relative',
            boxShadow: '0 0 4px rgba(0,0,0,.14),0 4px 8px rgba(0,0,0,.28)',
            overflow: 'hidden',
            maxWidth: '9999px',
            padding: '0px 0px'
          };
        return(
            <div>
                <div class="top">
                    <div class="logo header">
                        <img class="logo"src={logo}></img>
                    </div>

                    <div class="name">
                        Rebekah Rhodes Portfolio
                    </div>

                    <hr /> 

                </div>
                <div class="main">
                        <div class="container" onClick={() => this.onOpenModal(cat, "Cat Cat Cat Cat  Cat Cat  Cat Cat  Cat Cat  Cat Cat  Cat Cat  Cat Cat ", "Dirac")} >
                            <img class="material"src={cat}></img>      
                            <div class="overlay">
                             <div class="text">DIRAC</div>
                            </div>                      
                        </div>
                        
                       
                        <div class="container" onClick={() => this.onOpenModal(rain)}>
                            <img class="material"src={rain}></img>      
                            <div class="overlay">
                             <div class="text">RAIN</div>
                            </div>                      
                        </div>

                        <div class="container" onClick={() => this.onOpenModal(cat2)}>
                            <img class="material"src={cat2}></img>      
                            <div class="overlay">
                             <div class="text">KIT</div>
                            </div>                      
                        </div>

                        <div class="container" >
                            <img class="material"src={rain}></img>      
                            <div class="overlay">
                             <div class="text">RAIN</div>
                            </div>                      
                        </div>

                        <div class="container" onClick={() => this.onOpenModal(bridge)}>
                            <img class="material"src={bridge} ></img>      
                            <div class="overlay">
                             <div class="text">BRIDGE</div>
                            </div>                      
                        </div>

                        <div class="container" >
                            <img class="material"src={rain}></img>      
                            <div class="overlay">
                             <div class="text">RAIN</div>
                            </div>                      
                        </div>

                         <div class="container" onClick={() => this.onOpenModal(cat, "Cat Cat Cat Cat  Cat Cat  Cat Cat  Cat Cat  Cat Cat  Cat Cat  Cat Cat ", "Dirac")} >
                            <img class="material"src={cat}></img>      
                            <div class="overlay">
                             <div class="text">DIRAC</div>
                            </div>                      
                        </div>
                        
                       
                        <div class="container" onClick={() => this.onOpenModal(rain)}>
                            <img class="material"src={rain}></img>      
                            <div class="overlay">
                             <div class="text">RAIN</div>
                            </div>                      
                        </div>

                        <div class="container" onClick={() => this.onOpenModal(cat2)}>
                            <img class="material"src={cat2}></img>      
                            <div class="overlay">
                             <div class="text">KIT</div>
                            </div>                      
                        </div>

                </div>
                <div class="footer">
                    <hr/>
                    <h1>Author</h1>
                    <p>Rebekah Rhodes </p>
                </div>
                <div class="poppop">
                <PopPop open={open} 
                            onClose={this.onCloseModal} 
                            position="centerCenter" 
                            contentStyle={content}
                            closeBtn="true"
                            closeOnEsc="true"
                            >
                            <div class="modal">
                                <div class="modal left">
                                <img class="modalImg"src={this.state.modalImage}></img>      
                                </div>
                                <div class="modal right">
                                   <h1>{this.state.modal_title}</h1>
                                   <p>
                                      {this.state.modal_des}
                                   </p>
                                </div>
                            </div>
                        </PopPop>

            </div>
            </div>
        )
    }
}